<template>
    <div class="container">
        <button class="btn btn-default back" type="button" @click="$router.back(-1)">
            
        </button>
        <form class="form-signin con_box">

            <h2 class="login_title">{{ $route.params.msg }}</h2>
            <!--<button class="btn btn-default"></button>-->

            <input type="text" placeholder="" class="form-control" v-model="remark">

            <ul class="lists_content">
                <li v-for="(name,index) in types">
                    <span>{{name}}</span>
                    <label for="text" v-if="name == '* '"></label>
                    <label for="text" v-else-if="name == '* '"></label>
                    <label for="text" v-else-if="name == '* (20) '"> </label>
                    <label for="text" v-else-if="name == '* (20)'"> </label>
                    <label for="text" v-else-if="name == '* (20)'"> </label>
                    <!--<label for="text"></label>-->
                    <input type="text" id="text" v-model="inputData[index]">
                    <!--<input type="text" :id="index" v-if="name == '* '" v-model="inputData[index]">
                    <input type="text" :id="index" v-else-if="name == '* '" v-model="inputData2">
                    <input type="text" :id="index" v-else-if="name == '* (20) '" v-model="inputData3">
                    <input type="text" :id="index" v-else-if="name == '* (20)'" v-model="inputData4">
                    <input type="text" :id="index" v-else-if="name == '* (20)'" v-model="inputData5">-->
                </li>
            </ul>

            <div class="reg_btn">
                <button class="btn btn-default" type="button" @click="saveFn"></button>
            </div>

        </form>

    </div>
</template>

<script>
    export default {
        name: "projectdetail",
        data() {
            return {
                types:[
                    '* ',
                    '* ',
                    '* (20) ',
                    '* (20)',
                    '* (20)',
                ],
                inputData:[
                    '111',
                    '111',
                    '111',
                    '111',
                    '111',
                ],
                remark:''
            }

        },

        created() {

            this.$nextTick(function () {

            })
        },
        mounted(){
            if (JSON.parse(window.localStorage.getItem('inputData'))){
                this.inputData = JSON.parse(window.localStorage.getItem('inputData'));
                this.remark = window.localStorage.getItem('remark');
                // console.log(this.inputData)
            }

        },
        methods: {
            saveFn(){
                // console.log(JSON.stringify(this.inputData))
                window.localStorage.setItem('inputData',JSON.stringify(this.inputData));
                window.localStorage.setItem('remark',this.remark);
                this.$router.go(-1);
            }
        }
    }
</script>

<style scoped lang="less">
    .con_box {
        /*max-width: 650px;*/
        padding: 50px 100px;
        margin: 0 auto;
        /*border: 2px solid #666666;*/
        position: relative;
    }

    .form-signin .form-signin-heading,
    .form-signin .checkbox {
        margin-bottom: 10px;
    }

    .form-signin .checkbox {
        font-weight: normal;
    }

    .form-signin .form-control {
        width: 65%;
        height: 35px;
        margin: 0 auto;
        position: relative;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        padding: 9px;
        font-size: 16px;
    }


    .form-signin input[type="text"] {
        margin-bottom: 20px;
    }

    .form-signin input[type="password"] {
        margin-bottom: 20px;
    }

    .lists_content{
        padding: 0 70px;
        li{
            width: 100%;
            height: 30px;
            line-height: 30px;
            margin-bottom: 10px;
            span:nth-child(1){
                float: left;
            }
            label{
                float: right;
                font-weight: normal;
            }
            input[type="text"]{
                margin: 0;
                width: 70px;
                border-bottom: 1px solid #000;
                float: right;
            }
        }

    }
    .login_title {
        margin: 0;
        padding: 0;
        margin-bottom: 20px;
        font-size: 18px;
        text-align: center;
    }

   .number{
       width: 20%;
       border-bottom: 1px solid #000;
   }

    .reg_btn {
        width: 65%;
        text-align: center;
        margin: 0 auto;
        margin-top: 50px;
    }

    .reg_btn button {
        width: 30%;
    }

    .back{
        position: absolute;
        left: 15px;
        top: 10px;
        z-index: 99;
    }


</style>