<template>
    <div class="container">
        <form class="form-signin con_box">
            <button class="btn btn-default back" type="button" @click="$router.back(-1)">
                
            </button>

            <h2 class="login_title"></h2>

            <input type="text" id="company_name" class="form-control" placeholder="" required="" autofocus @change="companyFormat" v-model="company_name">
            <input type="text" id="user_name" class="form-control" placeholder="" required @change="nameFormat" v-model="name">
            <input type="text" id="phone" class="form-control" placeholder="" required @change="callFormat" v-model="phone">
            <input type="text" id="ver_code" class="form-control code_input" placeholder="" required v-model="verification_code">
            <input type="button" class="form-control generate_code" value="" @click="setTime">
            <input type="password" id="inputPassword1" class="form-control" placeholder="" required v-model="password" @change="passwordFormat">
            <input type="password" id="inputPassword2" class="form-control" placeholder="" required v-model="confirm_password" @change="passwordFormat">

            <div class="reg_btn">
                <button class="btn btn-default" type="button" @click="regSubmit"></button>
            </div>

        </form>

    </div>
</template>

<script>
    export default {
        name: "register",
        data() {
            return {
                countdown: 60,
                company_name: '',
                name: '',
                phone: '17681815401',
                verification_code: '1234',
                password: '123qwe',
                confirm_password: '123qwe',
            }

        },
        mounted(){

        },
        methods: {
            companyFormat(e){
                let nameVal = e.target.value;
                const regName = /^[\u4E00-\u9FA5]{2,20}$/;
                if (!regName.test(nameVal)) {
                    $('#company_name').val('');
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                    return false;
                }
            },
            passwordFormat(e){
                let nameVal = e.target.value;
                const regName = /^[a-zA-Z]\w{5,15}$/;
                if (!regName.test(nameVal)) {
                    // $('#inputPassword1').val('');
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                    return false;
                }
            },
            nameFormat(e) {
                // console.log(e);
                let nameVal = e.target.value;
                const regName = /^[\u4E00-\u9FA5]{2,8}$/;
                if (!regName.test(nameVal)) {
                    $('#user_name').val('');
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                    return false;
                }
            },
            callFormat(e) {
                // console.log(e);
                let callVal = e.target.value;
                const regName = /^(((1[3456789][0-9]{1})|(15[0-9]{1}))+\d{8})$/;
                if (!regName.test(callVal)) {
                    $('#phone').val('');
                    this.$message({
                        message: '',
                        type: 'error'
                    });
                    return false;
                }
            },
            setTime(e) {
                const that = this;
                const target = e;

                this.$axios({
                    method: 'post',
                    url: '/api/verificationCode',
                    data: {
                        type: 'register',
                        phone: this.phone,
                    }
                }).then(function (response) {

                    that.$message({
                        message: response.data.msg,
                        type: 'success'
                    })
                    // this.$router.go(-1);
                    console.log(response)
                    // console.log(response.data)
                }).catch(function (error) {

                    console.log(error);
                });

                countDown(target);
                //
                function countDown(e) {
                    if (that.countdown == 0) {
                        e.target.removeAttribute("disabled");
                        e.target.value = "";
                        that.countdown = 60;
                        return false;
                    } else {
                        e.target.setAttribute("disabled", true);
                        e.target.value = "(" + that.countdown + ")";
                        that.countdown--;
                    }
                    setTimeout(function () {
                        countDown(target);
                    }, 1000);
                }
            },
            regSubmit(){
                const that = this;
                //
                if (this.company_name == ''||this.name == ''||this.phone == ''||this.verification_code == ''||this.password == ''||this.confirm_password == ''){
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                    return false;
                }
                //
                if (this.password == this.confirm_password){
                    const loading = this.$message({
                        message: '',
                        type: 'loading'
                    })

                    this.$axios({
                        method: 'post',
                        url: '/api/users',
                        data: {
                            company_name: this.company_name,
                            name: this.name,
                            phone: this.phone,
                            verification_code: this.verification_code,
                            password: this.password,
                            password_confirmation: this.confirm_password
                        }
                    }).then(function (response) {
                        loading.close();
                        that.$message({
                            message: response.data.msg,
                            type: 'success'
                        })

                        if (response.data.status == 0){
                            that.$store.commit('companyName', that.company_name)
                            that.$store.commit('userName', that.name)
                            that.$store.commit('userPhone', that.phone)

                            that.$router.push({ name: 'login', params: { phone: that.phone}})
                        }



                        // this.$router.go(-1);
                        console.log(response)
                    }).catch(function (error) {
                        console.log(error);
                    });
                }else{
                    this.$message({
                        message: ',',
                        type: 'error'
                    })
                }
            }
        }
    }
</script>

<style scoped>
    .container{
        position: relative;
        top: 15%;
    }
    .con_box {
        max-width: 650px;
        padding: 30px 130px 50px;
        margin: 0 auto;
        border: 2px solid #666666;
        position: relative;
    }

    .form-signin .form-signin-heading,
    .form-signin .checkbox {
        margin-bottom: 10px;
    }

    .form-signin .checkbox {
        font-weight: normal;
    }

    .form-signin .form-control {
        width: 65%;
        height: 35px;
        margin: 0 auto;
        position: relative;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        padding: 9px;
        font-size: 16px;
    }

    /*.form-signin .form-control:focus {
        z-index: 2;
    }*/

    .form-signin input[type="text"] {
        margin-bottom: 20px;
    }

    .form-signin input[type="password"] {
        margin-bottom: 20px;
    }


    .login_title {
        margin: 0;
        padding: 0;
        margin-bottom: 20px;
        font-size: 18px;
        text-align: center;
    }

    /**/
    .form-signin .code_input {
        width: 30%;
        margin: 0;
        display: inline-block;
    }

    .form-signin .generate_code {
        width: 30%;
        margin: 0 0 0 15px;
        display: inline-block;
        font-size: 12px;
    }

    .reg_btn {
        width: 65%;
        text-align: center;
        margin: 0 auto;
        margin-top: 20px;
    }

    .reg_btn button {
        width: 30%;
    }

    .back{
        position: absolute;
        left: 15px;
        top: 10px;
    }

</style>