            </ul>
        </div>

        <div class="M-box m-style"></div>

        <div class="bg_shade" v-if="commonDataState"></div>

        <transition name="slide-fade">
            <CommonData v-if="commonDataState" @showState="getMsg"></CommonData>
        </transition>

    </div>
</template>

<script>
    import CommonData from '../components/CommonData'

    export default {
        name: "projectlists",
        data() {
            return {
                lists: {
                    name: [],
                    id: []
                },
                userName: '',
                commonDataState: false,
                permission: 0,
                pageNum: 1,
                listsArr: [],
                listsPage: [],
                listsId: [],
                listsNum: 10,
            }
        },
        components: {
            CommonData
        },
        created() {
            const that = this;

            if (window.localStorage.getItem('userName')) {
                this.userName = window.localStorage.getItem('userName');
                this.permission = window.localStorage.getItem('permission');
                
            } else {
                // 
                this.$axios.interceptors.request.use(config => {
                    // 
                    //tokenheadertoken
                    if (this.$store.state.token) {
                        config.headers.Authorization = this.$store.state.token
                    }
                    return config;
                }, error => {
                    // 
                    return Promise.reject(error);
                });

                this.$axios({
                    method: 'post',
                    url: '/api/me'
                }).then(function (response) {

                    that.$store.commit('companyName', response.data.company_name)
                    that.$store.commit('userName', response.data.name)
                    that.$store.commit('userPhone', response.data.phone)
                    that.$store.commit('permission', response.data.is_admin)
                    that.$store.commit('item_ids', response.data.item_ids)

                    // that.$router.push({ name: 'login', params: { phone: that.phone}})

                    // this.$router.go(-1);
                    that.userName = response.data.name;
                    that.permission = response.data.is_admin;
                    console.log(that.permission)

                    console.log(response)
                }).catch(function (error) {
                    console.log(error);
                });

            }

            this.getPageInit();
        },
        mounted() {
            if (this.$route.params.userName) {
                this.userName = this.$route.params.userName;
            }
        },
        methods: {
            getPageInit() {
                const that = this;

                if (this.listsPage.indexOf(this.pageNum) == -1) {
                    this.listsPage.push(this.pageNum)

                    this.$axios({
                        method: 'get',
                        url: '/api/projects?page=' + this.pageNum
                    }).then(function (response) {

                        response.data.data.forEach(function (project) {
                            // that.listsArr.push(project.name)
                            // that.listsId.push(project.id)

                            that.listsArr.push(project.name)
                            that.listsId.push(project.id)
                        })

                        if (that.pageNum == 1) {
                            //
                            for (let i = 0; i < 10; i++) {
                                that.lists.name.push(that.listsArr[i]);
                                that.lists.id.push(that.listsId[i]);
                            }
                        } else {
                            //
                            that.lists = {name: [], id: []};
                            for (let i = -10 + that.listsNum; i < that.listsNum; i++) {
                                that.lists.name.push(that.listsArr[i]);
                                that.lists.id.push(that.listsId[i]);
                            }

                        }

                        $('.M-box').pagination({
                            mode: 'fixed',
                            current: response.data.current_page,
                            // pageCount: Math.ceil(listsArr.length / 10),
                            pageCount: response.data.last_page,
                            callback: function (api) {
                                that.pageNum = api.getCurrent();
                                that.listsNum = that.pageNum * 10;

                                that.getPageInit();

                                //
                                if (that.listsPage.indexOf(that.pageNum) != -1) {
                                    //
                                    that.lists = {name: [], id: []};
                                    for (let i = -10 + that.listsNum; i < that.listsNum; i++) {
                                        that.lists.name.push(that.listsArr[i]);
                                        that.lists.id.push(that.listsId[i]);
                                    }
                                }

                                // console.log(that.pageNum)
                                // console.log(that.listsArr)
                            }
                        }, function (api) {
                            // console.log(api)
                        });


                        // pageFn(response.data.last_page,response.data.current_page,that.listsArr)

                        // this.$router.go(-1);
                        // that.userName = response.data.name;
                        // that.permission = response.data.is_admin;

                        console.log(response)
                    }).catch(function (error) {
                        console.log(error);
                    });

                }


            },
            commonDataFn() {
                this.commonDataState = true;
            },
            getMsg(msg) {
                this.commonDataState = msg;
            }
        }
    }
</script>

