        methods: {
            companyFormat(e) {
                // console.log(e);
                let nameVal = e.target.value;
                const regName = /^[\u4E00-\u9FA5]{2,20}$/;
                if (!regName.test(nameVal)) {
                    $('#companyName').val('');
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                    return false;
                }
            },
            personSubmit() {
                const that = this;
                //
                if (this.companyName == '' || $('.filter-option').text() == '') {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                    return false;
                } else {

                    let typeArray = $('.filter-option').text().split(",");
                    this.typeArrayId = [];

                    $('.selectpicker li.selected').each(function (index, name) {
                        that.typeArrayId.push($(name).index() + 1);
                    })

                    this.$axios({
                        method: 'post',
                        url: '/api/motifyUserInfo',
                        data: {
                            company_name: this.companyName,
                            items: this.typeArrayId,
                            wechat: this.weixinName
                        }
                    }).then(function (response) {

                        that.$message({
                            message: response.data.msg,
                            type: 'success'
                        })

                        that.$store.commit('companyName', that.companyName)
                        that.$store.commit('supplyTypeArr', typeArray)

                        that.$router.push({path: 'projectlists'})

