<template>
    <div class="container">

        <button class="btn btn-default view" type="button" v-if="permission == 1">
            <router-link :to="{name:'projecttable',params:{msg:pojectMsg.name}}" ></router-link>
        </button>

        <form class="form-signin con_box">

            <h2 class="login_title">{{$route.params.msg}}</h2>
            <div class="project_content">
                <ul>
                    <li>
                        <span>{{projectName.area}}</span>
                    </li>
                    <li>
                        <span>{{projectName.aboveArea}}</span>
                    </li>
                    <li>
                        <span>{{projectName.aboveAreaCoe}}</span>
                    </li>
                    <li>
                        <span>{{projectName.underArea}}</span>
                    </li>
                    <li>
                        <span>{{projectName.underAreaCoe}}</span>
                    </li>
                    <li>
                        <span>{{projectName.reference_price}}</span>
                    </li>
                    <li>
                        <span>{{projectName.intro}}</span>
                    </li>
                </ul>

                <!--<ul>
                    <li>
                        <span>10000</span>
                    </li>
                    <li>
                        <span>5000</span>
                    </li>
                    <li>
                        <span>5</span>
                    </li>
                    <li>
                        <span>5000</span>
                    </li>
                    <li>
                        <span>5</span>
                    </li>
                    <li>
                        <span>200</span>
                    </li>
                    <li>
                        <span></span>
                    </li>
                </ul>-->
            </div>

            <div class="listsType">
                <ul>
                    <li v-for="name in types">
                        <span>{{name}}</span>
                        <router-link :to="{name:'projectview',params:{msg:name}}" v-if="permission == 1"></router-link>
                        <router-link :to="{name:'projectview',params:{msg:name}}" v-else-if="permission == 0"></router-link>
                        <!--<a href="https://forreall.cn/3ds/app/kp-tender/" v-else-if="permission == 0"></a>-->
                    </li>

                </ul>
            </div>

            <a :href="projectName.attachment" download="" class="down_drawing"></a>


            <div class="reg_btn">
                <button class="btn btn-default" type="button" @click="$router.back(-1)"></button>
            </div>

        </form>

    </div>
</template>

<script>
    export default {
        name: "projectedit",
        data() {
            return {
                countdown: 10,
                types:[
                    '',
                    '',
                    '',
                    // '',
                    // '',
                    // '',
                    // '',
                    // '',

                ],
                projectName:{
                    "name":null,
                    "area":null,
                    "aboveArea":null,
                    "aboveAreaCoe":null,
                    "underArea":null,
                    "underAreaCoe":null,
                    "intro":null,
                    "reference_price":null,
                    "attachment":null,
                },
                permission:0,
                pojectMsg:{},
                projectId:0
            }

        },

        created() {

            this.permission = window.localStorage.getItem('permission');
            // this.types = JSON.parse(window.localStorage.getItem('subitem'));

            this.projectId = this.$route.params.id;
            console.log(this.projectId)

            // 
            this.$axios.interceptors.request.use(config => {
                // 
                //tokenheadertoken
                if (this.$store.state.token) {
                    config.headers.Authorization = this.$store.state.token
                }
                return config;
            }, error => {
                // 
                return Promise.reject(error);
            });

        },
        mounted(){
            const that = this;

            this.$axios({
                method: 'get',
                url: '/api/items'
            }).then(function (response) {

                // that.projectName.reference_price = response.data.reference_price;
                // that.projectName.attachment = response.data.attachment;

                // pageFn(response.data.last_page,response.data.current_page,that.listsArr)

                // this.$router.go(-1);
                // that.userName = response.data.name;
                // that.permission = response.data.is_admin;

                console.log(response)
            }).catch(function (error) {
                console.log(error);
            });


           /* this.$axios({
                method: 'get',
                url: '/api/project_items?project_id='+this.projectId
            }).then(function (response) {

                that.projectName.reference_price = response.data.reference_price;
                that.projectName.attachment = response.data.attachment;

                // pageFn(response.data.last_page,response.data.current_page,that.listsArr)

                // this.$router.go(-1);
                // that.userName = response.data.name;
                // that.permission = response.data.is_admin;

                console.log(response)
            }).catch(function (error) {
                console.log(error);
            });*/



            this.$axios({
                method: 'get',
                url: '/api/project?project_id='+this.projectId
                /*data: {
                    project_id:59
                }*/
            }).then(function (response) {

                that.projectName.name = response.data.name;
                that.projectName.area = response.data.area;
                that.projectName.aboveArea = response.data.above_area;
                that.projectName.aboveAreaCoe = response.data.above_open_ratio;
                that.projectName.underArea = response.data.below_area;
                that.projectName.underAreaCoe = response.data.below_open_ratio;
                that.projectName.intro = response.data.intro;
                that.projectName.reference_price = response.data.reference_price;
                that.projectName.attachment = response.data.attachment;

                // pageFn(response.data.last_page,response.data.current_page,that.listsArr)

                // this.$router.go(-1);
                // that.userName = response.data.name;
                // that.permission = response.data.is_admin;

                console.log(response)
            }).catch(function (error) {
                console.log(error);
            });


        },
        methods: {

        }
    }
</script>

<style scoped lang="less">
    .con_box {
        /*max-width: 650px;*/
        padding: 50px 100px;
        margin: 0 auto;
        /*border: 2px solid #666666;*/
        position: relative;
    }

    .form-signin .form-signin-heading,
    .form-signin .checkbox {
        margin-bottom: 10px;
    }

    .form-signin .checkbox {
        font-weight: normal;
    }

    .form-signin .form-control {
        width: 65%;
        height: 35px;
        margin: 0 auto;
        position: relative;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        padding: 9px;
        font-size: 16px;
    }

    /*.form-signin .form-control:focus {
        z-index: 2;
    }*/

    .form-signin input[type="text"] {
        margin-bottom: 20px;
    }

    .form-signin input[type="password"] {
        margin-bottom: 20px;
    }


    .login_title {
        margin: 0;
        padding: 0;
        margin-bottom: 20px;
        font-size: 18px;
        text-align: center;
    }

    /**/
    .form-signin .code_input {
        width: 30%;
        margin: 0;
        display: inline-block;
    }

    .form-signin .generate_code {
        width: 30%;
        margin: 0 0 0 15px;
        display: inline-block;
        font-size: 12px;
    }

    .reg_btn {
        width: 65%;
        text-align: center;
        margin: 0 auto;
        margin-top: 20px;
    }

    .reg_btn button {
        width: 20%;
    }

    .project_content{
        width: 80%;
        margin: 0 auto;
        ul{
            li{
                font-size: 13px;
                text-align: left;
            }
        }
    }
    .listsType{
        height: 50vh;
        ul{
            height: 100%;
            padding: 0 20px;
            overflow-y: auto;
            li{
                width: 100%;
                height: 30px;
                line-height: 30px;
                span{
                    float: left;
                }
                a{
                    float: right;
                }
            }
        }
    }

    .back{
        position: absolute;
        left: 15px;
        top: 10px;
    }
    .view{
        position: absolute;
        right: 15px;
        top: 10px;
        z-index: 99;
    }

    .down_drawing{
        display: inline-block;
        margin-top: 20px;
    }

</style>