import Vue from 'vue'
import Router from 'vue-router'

import Login from './views/Login.vue'
import Register from './views/Register.vue'
import Changepw from './views/ChangePW.vue'
import Projectlists from './views/ProjectLists.vue'
import Changedata from './views/ChangeData.vue'
import Startproject from './views/StartProject.vue'
import Projectedit from './views/ProjectEdit.vue'
import Projectview from './views/ProjectView.vue'
import Projectdetail from './views/ProjectDetail.vue'
import Projecttable from './views/ProjectTable.vue'

Vue.use(Router)

export default new Router({
    // mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'login',
            component: Login
        },
        {
            path: '/register',
            name: 'register',
            component: Register
            // meta: {auth:true}
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            // component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
        },
        {
            path: '/changepw',
            name: 'changepw',
            component: Changepw
            // meta: {auth:true}

        },
        {
            path: '/projectlists',
            name: 'projectlists',
            component: Projectlists,
            meta: {auth:true}
        },
        {
            path: '/changedata',
            name: 'changedata',
            component: Changedata,
            meta: {auth:true}
        },
        {
            path: '/startproject/:id',
            name: 'startproject',
            component: Startproject,
            meta: {
                auth:true,
                keepAlive: true
            }
        },
        {
            path: '/projectedit/:msg/:id',
            name: 'projectedit',
            component: Projectedit,
            meta: {
                auth:true,
                keepAlive: true, // 
                isBack:false, //
            }
        },
        {
            path: '/projectview/:msg',
            name: 'projectview',
            component: Projectview,
            meta: {
                auth:true,
                keepAlive: true, // 
                isBack:false, //
            }
        },
        {
            path: '/projectdetail/:msg',
            name: 'projectdetail',
            component: Projectdetail,
            meta: {auth:true}

        },
        {
            path: '/projecttable',
            name: 'projecttable',
            component: Projecttable,
            meta: {auth:true}

        },
    ]
})
