<template>
    <div class="container">
        <button class="btn btn-default back" type="button" @click="$router.back(-1)">
            
        </button>
        <form class="form-signin con_box">

            <h2 class="login_title">{{ $route.params.msg }}</h2>
            <!--<button class="btn btn-default"></button>-->

            <!--<input type="text" placeholder="" class="form-control">-->

            <ul class="lists_content">
                <li v-for="name in types">
                    <span>{{name}}</span>

                    <div v-if="permission == 0">
                        <label for="text" ></label>
                        <input type="text" id="text" >
                    </div>

                    <span v-if="name == '* '">{{inputData[0]}}</span>
                    <span v-else-if="name == '* '">{{inputData[1]}}</span>
                    <span v-else-if="name == '* (20) '">{{inputData[2]}}</span>
                    <span v-else-if="name == '* (20)'">{{inputData[3]}}</span>
                    <span v-else-if="name == '* (20)'">{{inputData[4]}}</span>
                </li>
            </ul>

            <!--<a href="" download="" class="down_drawing"></a>-->

            <!--<div class="reg_btn">
                <button class="btn btn-default" type="button" @click="saveFn"></button>
            </div>-->

        </form>

    </div>
</template>

<script>
    export default {
        name: "projectview",
        data() {
            return {
                types:[
                    '* ',
                    '* ',
                    '* (20) ',
                    '* (20)',
                    '* (20)',
                ],
                permission:0,
                inputData:[

                ]
            }

        },

        created() {

            if (JSON.parse(window.localStorage.getItem('inputData'))){
                this.inputData = JSON.parse(window.localStorage.getItem('inputData'));
            }

            this.permission = window.localStorage.getItem('permission');
            console.log(this.inputData)

            this.$nextTick(function () {

            })
        },
        mounted(){

        },
        methods: {
            saveFn(){
                this.$router.go(-1);
            }
        }
    }
</script>

<style scoped lang="less">
    .con_box {
        /*max-width: 650px;*/
        padding: 50px 100px;
        margin: 0 auto;
        /*border: 2px solid #666666;*/
        position: relative;
    }

    .form-signin .form-signin-heading,
    .form-signin .checkbox {
        margin-bottom: 10px;
    }

    .form-signin .checkbox {
        font-weight: normal;
    }

    .form-signin .form-control {
        width: 65%;
        height: 35px;
        margin: 0 auto;
        position: relative;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        padding: 9px;
        font-size: 16px;
    }


    .form-signin input[type="text"] {
        margin-bottom: 20px;
    }

    .form-signin input[type="password"] {
        margin-bottom: 20px;
    }

    .lists_content{
        padding: 0 70px;
        li{
            width: 100%;
            height: 30px;
            line-height: 30px;
            margin-bottom: 10px;
            span:nth-child(1){
                float: left;
            }
            span:nth-child(2){
                float: right;
                margin-right: 50px;
            }
            span:nth-child(3){
                float: right;
                margin-right: 50px;
            }
            label{
                float: right;
                font-weight: normal;
            }
            input[type="text"]{
                margin: 0;
                width: 70px;
                border-bottom: 1px solid #000;
                float: right;
            }
        }

    }
    .login_title {
        margin: 0;
        padding: 0;
        margin-bottom: 20px;
        font-size: 18px;
        text-align: center;
    }
    .down_drawing{
        display: inline-block;
        margin-top: 20px;
    }

    .number{
        width: 20%;
        border-bottom: 1px solid #000;
    }

    .reg_btn {
        width: 65%;
        text-align: center;
        margin: 0 auto;
        margin-top: 50px;
    }

    .reg_btn button {
        width: 30%;
    }

    .back{
        position: absolute;
        left: 15px;
        top: 10px;
        z-index: 99;
    }


</style>