<template>
    <div class="container">


        <form class="form-signin con_box">
            <h2 class="login_title"></h2>

            <input type="text" id="phone" class="form-control" placeholder="" required="required" autofocus
                   v-model="phone" @change="callFormat">

            <!--<span id="helpBlock" class="help-block  btn-warning">.</span>-->

            <input type="password" id="inputPassword" class="form-control" placeholder="" required=""
                   v-model="password">
            <div class="reg_now">
                <span>?</span>
                <router-link to="/register"></router-link>
                <router-link to="/changepw" class="change_pw">?</router-link>
            </div>

            <div class="btn_type">
                <!--<router-link to="/register" class="btn btn-default"></router-link>-->
                <button class="btn btn-default" type="button" @click="loginFn">
                    <!--<router-link :to="{name:'projectlists',params:{id:num}}"></router-link>-->
                    <!--<router-link to="/projectlists"></router-link>-->
                    
                </button>
            </div>
        </form>

        <!--<uploader :options="options" class="uploader-example">
            <uploader-unsupport></uploader-unsupport>
            <uploader-drop>
                <p>Drop files here to upload or</p>
                <uploader-btn>select files</uploader-btn>
                &lt;!&ndash;<uploader-btn :attrs="attrs">select images</uploader-btn>&ndash;&gt;
