            </uploader-drop>
            <uploader-list></uploader-list>
        </uploader>-->


    </div>
</template>

<script>
    // import { mapMutations } from 'vuex';
    export default {
        name: 'Login',
        data() {
            return {
                num: 123,
                phone: '17681815555',
                password: 'qweqweqwe',
                /*options: {
                    // https://github.com/simple-uploader/Uploader/tree/develop/samples/Node.js
                    target: '//localhost:8001',
                    testChunks: false
                },
                attrs: {
                    accept: 'image/!*'
                }*/
            }
        },
        /* beforeCreate(){
             console.log('beforeCreate')
         },
         created(){
             console.log('create')
         },
         beforeMount(){
             console.log('beforeMount')
         },
         beforeUpdate(){
             console.log('beforeUpdate')
         },
         updated(){
             console.log('updated')
         },
         activated(){
             console.log('activated')
         },
         destroyed () {
             console.log('destroyed')
         },*/
        mounted() {
            if (this.$route.params.phone) {
                this.phone = this.$route.params.phone;
            }
        },
        methods: {
            // ...mapMutations(['changeLogin']),
            callFormat(e) {
                // console.log(e);
                let callVal = e.target.value;
                const regName = /^(((1[3456789][0-9]{1})|(15[0-9]{1}))+\d{8})$/;
                if (!regName.test(callVal)) {
                    $('#phone').val('');
                    this.$message({
                        message: '',
                        type: 'error'
                    });
                    return false;
                }
            },
            loginFn() {

                //
                if (this.phone == '' || this.password == '') {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                    return false;
                } else {
                    const that = this;

                    this.$axios({
                        method: 'post',
                        url: '/api/login',
                        data: {
                            phone: this.phone,
                            password: this.password
                        }
                    }).then(function (response) {
                        if (response.data.status == 0) {
                            that.$message({
                                message: response.data.msg,
                                type: 'success'
                            })

                            // window.localStorage.clear()
                            window.localStorage.removeItem('permission')
                            window.localStorage.removeItem('companyName')
                            window.localStorage.removeItem('item_ids')
                            window.localStorage.removeItem('pojectMsg')
                            window.localStorage.removeItem('token')
                            window.localStorage.removeItem('checkeds')
                            window.localStorage.removeItem('userPhone')
                            window.localStorage.removeItem('userName')

                            that.$store.commit('userPhone', that.phone)
                            // tokenvuex
                            that.$store.commit('set_token',response.headers.authorization)

                            that.$router.push({name: 'projectlists', params: {phone: that.phone}})
                        }else {
                            that.$message({
                                message: response.data.msg,
                                type: 'error'
                            })
                        }

                        console.log(response)
                    }).catch(function (error) {
                        console.log(error);
                    });
                }
            }
        }
    }
</script>

