<template>
    <div class="container_box">

        <form class="form-signin con_box">

            <button class="btn btn-default back" type="button" @click="$router.back(-1)">
                
            </button>

            <!--<h2 class="login_title">{{ $route.params.id}}</h2>-->

            <h2 class="login_title">{{pojectMsg.name}}</h2>
            <div class="project_content">
                <ul>
                    <li>
                        <span>{{pojectMsg.area}}</span>
                    </li>
                    <li>
                        <span>{{pojectMsg.above_area}}</span>
                    </li>
                    <li>
                        <span>{{pojectMsg.above_open_ratio}}</span>
                    </li>
                    <li>
                        <span>{{pojectMsg.below_area}}</span>
                    </li>
                    <li>
                        <span>{{pojectMsg.below_open_ratio}}</span>
                    </li>
                    <li>
                        <span>{{pojectMsg.reference_price}}</span>
                    </li>
                    <li>
                        <span>{{pojectMsg.intro}}</span>
                    </li>
                </ul>

            </div>
            <div class="content_lists">
                <ul>
                    <li v-for="(name,index) in typesArr" :key="index">
                        <input type="checkbox" name="typeName" :id="index" class="myCheck" :value="name" @change="checkedState">
                        <label :for="index"></label>
                        {{name}}

                        <router-link :to="{name:'projectdetail',params:{msg:name}}" class="type_edit"></router-link>

                    </li>

                </ul>
            </div>


            <div class="reg_btn">
                <button class="btn btn-default" type="button" @click="showDataFn">
                    
                    <!--<router-link :to="{name:'projectlists'}"></router-link>-->
                </button>
            </div>

        </form>

        <!--<transition name="slide-fade">
            <CommonData v-if="showData"></CommonData>
        </transition>-->


    </div>
</template>

<script>

    import CommonData from '../components/CommonData'

    export default {
        name: "startproject",
        data() {
            return {
                countdown: 10,
                showData: false,
                typesArr: [
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                    '',
                ],
                checkeds:[],
                pojectMsg:{},
                projectId:null
            }

        },
        components: {
            CommonData
        },
        created() {
            this.projectId = this.$route.params.id;
            console.log(this.projectId)
        },
        mounted(){
            this.pojectMsg = JSON.parse(window.localStorage.getItem('pojectMsg'));
            if (window.localStorage.getItem('checkeds')){

                this.checkeds = JSON.parse(localStorage.getItem('checkeds'));

                this.checkeds.forEach(function (name) {
                    $('input').eq(name).attr('checked','true').siblings('a').fadeIn(100);
                })
            }
        },
        methods: {
            checkedState(e) {
                // console.log(e)
                const that = this;
                let inputId = e.target.id;
                let $input = $('#' + inputId);

                $input.is(':checked') ? $input.siblings('a').fadeIn(100):$input.siblings('a').fadeOut(100)

                let indexArr = [];
                $('input').each(function (index,name) {
                    if ($(name).is(':checked')){
                        indexArr.push(index);
                    }

                })

                this.$store.commit('checkeds',indexArr)

            },
            showDataFn() {
                this.showData = true;
                // js
                var test_list = [];
                $("input:checked").each(function (index,name) {
                    // test_list.push($(this).attr('value'))
                    test_list.push(index+1)
                });

                // window.localStorage.setItem('subitem',JSON.stringify(test_list));

                const that = this;
                this.$axios({
                    method: 'post',
                    url: '/api/project_items',
                    data:{
                        project_id:this.projectId,
                        items:test_list
                    }
                }).then(function (response) {

                    // pageFn(response.data.last_page,response.data.current_page,that.listsArr)

                    // this.$router.go(-1);
                    // that.userName = response.data.name;
                    // that.permission = response.data.is_admin;

                    that.$router.push({name: 'projectlists'})


                    console.log(response)
                }).catch(function (error) {
                    console.log(error);
                });

                console.log(test_list)

                // console.log(JSON.stringify(test_list)); // json


            }
        }
    }
</script>

<style scoped lang="less">
    .container_box {
        position: relative;
    }

    .con_box {
        /*max-width: 650px;*/
        padding: 30px 130px 50px;
        margin: 0 auto;
        /*border: 2px solid #666666;*/
        height: 450px;
        position: relative;
    }

    .content_lists {
        width: 85%;
        height: 50vh;
        overflow-y: auto;
        margin: 0 auto;

        ul li {
            line-height: 35px;
            text-align: left;
        }
    }

    .myCheck + label {
        background-color: white;
        border: 1px solid #d3d3d3;
        width: 20px;
        height: 20px;
        display: inline-block;
        text-align: center;
        vertical-align: middle;
        line-height: 20px;
    }

    .myCheck:checked + label {
        background-color: #eee;
        background: #eee url("../assets/img/.png") no-repeat center/contain;
    }

    .login_title {
        margin: 0;
        padding: 0;
        margin-bottom: 20px;
        font-size: 18px;
        text-align: center;
    }


    .reg_btn {
        width: 65%;
        text-align: center;
        margin: 0 auto;
        margin-top: 20px;
    }

    .reg_btn button {
        width: 20%;
    }

    .back {
        position: absolute;
        left: 15px;
        top: 10px;
    }

    .type_edit {
        margin-right: 20px;
        float: right;
        display: none;
    }

    .project_content{
        width: 80%;
        margin: 0 auto;
        ul{
            li{
                font-size: 13px;
                text-align: left;
            }
        }
    }


</style>