<template>
    <div class="container">

        <form class="form-signin con_box">

            <button class="btn btn-default back" type="button" @click="$router.back(-1)">
                
            </button>

            <h2 class="login_title"></h2>

            <input type="text" id="inputCall" class="form-control" placeholder="" required @change="callFormat" v-model="phone">
            <input type="text" id="ver_code" class="form-control code_input" placeholder="" required v-model="verification_code">
            <input type="button" class="form-control generate_code" value="" @click="setTime">
            <input type="password" id="inputPassword1" class="form-control" placeholder="" required v-model="password">
            <input type="password" id="inputPassword2" class="form-control" placeholder="" required v-model="confirm_password">

            <div class="reg_btn">
                <button class="btn btn-default" type="button" @click="passwordSubmit"></button>
            </div>

        </form>

    </div>
</template>

<script>
    export default {
        name: "changepw",
        data() {
            return {
                countdown: 60,
                phone: '',
                verification_code: '',
                password: '',
                confirm_password: '',
            }

        },
        methods: {
            callFormat(e){
                console.log(e)
                let callVal = e.target.value;
                const regName = /^(((1[3456789][0-9]{1})|(15[0-9]{1}))+\d{8})$/;
                if(!regName.test(callVal)){
                    $('#inputCall').val('');
                    this.$message({
                        message: '',
                        type: 'error'
                    })
                    return false;
                }
            },
            setTime(e) {
                const that = this;
                const target = e;

                this.$axios({
                    method: 'post',
                    url: '/api/verificationCode',
                    data: {
                        type: 'modifyPassword',
                        phone: this.phone,
                    }
                }).then(function (response) {

                    that.$message({
                        message: response.data.msg,
                        type: 'success'
                    })
                    // this.$router.go(-1);
                    console.log(response)
                    // console.log(response.data)
                }).catch(function (error) {

                    console.log(error);
                });

                countDown(target);
                //
                function countDown(e) {
                    if (that.countdown == 0) {
                        e.target.removeAttribute("disabled");
                        e.target.value = "";
                        that.countdown = 60;
                        return false;
                    } else {
                        e.target.setAttribute("disabled", true);
                        e.target.value = "(" + that.countdown + ")";
                        that.countdown--;
                    }
                    setTimeout(function () {
                        countDown(target);
                    }, 1000);
                }
            },
            passwordSubmit(){
                const that = this;
                //
                if (this.phone == ''||this.verification_code == ''||this.password == ''||this.confirm_password == ''){
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                    return false;
                }
                //
                if (this.password == this.confirm_password){
                    const loading = this.$message({
                        message: '',
                        type: 'loading'
                    })

                    this.$axios({
                        method: 'post',
                        url: '/api/modifyPassword',
                        data: {
                            phone: this.phone,
                            verification_code: this.verification_code,
                            password: this.password,
                            password_confirmation: this.confirm_password
                        }
                    }).then(function (response) {
                        loading.close();
                        that.$message({
                            message: response.data.msg,
                            type: 'success'
                        })

                        that.$router.push({ name: 'login', params: { phone: that.phone}})

                        // this.$router.go(-1);
                        console.log(response)
                    }).catch(function (error) {
                        console.log(error);
                    });
                }else{
                    this.$message({
                        message: ',',
                        type: 'error'
                    })
                }
            }
        }
    }
</script>

<style scoped>
    .container{
        position: relative;
        top: 15%;
    }
    .con_box {
        max-width: 650px;
        padding: 30px 130px 50px;
        margin: 0 auto;
        border: 2px solid #666666;
        position: relative;
    }

    .form-signin .form-signin-heading,
    .form-signin .checkbox {
        margin-bottom: 10px;
    }

    .form-signin .checkbox {
        font-weight: normal;
    }

    .form-signin .form-control {
        width: 65%;
        height: 35px;
        margin: 0 auto;
        position: relative;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        padding: 9px;
        font-size: 16px;
    }

    /*.form-signin .form-control:focus {
        z-index: 2;
    }*/

    .form-signin input[type="text"] {
        margin-bottom: 20px;
    }

    .form-signin input[type="password"] {
        margin-bottom: 20px;
    }


    .login_title {
        margin: 0;
        padding: 0;
        margin-bottom: 20px;
        font-size: 18px;
        text-align: center;
    }

    /**/
    .form-signin .code_input {
        width: 30%;
        margin: 0;
        display: inline-block;
    }

    .form-signin .generate_code {
        width: 30%;
        margin: 0 0 0 15px;
        display: inline-block;
        font-size: 12px;
    }

    .reg_btn {
        width: 65%;
        text-align: center;
        margin: 0 auto;
        margin-top: 20px;
    }

    .reg_btn button {
        width: 30%;
    }

    .back{
        position: absolute;
        left: 15px;
        top: 10px;
    }

</style>