<template>
    <div class="container">
        <button class="btn btn-default back" @click="setMsg"></button>
        <form class="form-signin con_box">

            <h2 class="login_title"></h2>

            <div class="form-group">
                <label class="col-sm-3" for="poject_name"></label>
                <div class="col-sm-6">
                    <input type="text" class="form-control" id="poject_name" required v-model="poject_name">
                </div>
            </div>

            <div class="form-group">
                <label class="col-sm-3" for="poject_name"></label>
                <div class="col-sm-3">
                    <input type="text" class="form-control" id="poject_name" required v-model="area">
                </div>
                <div class="col-sm-1 unit">
                    
                </div>
            </div>

            <div class="form-group">
                <label class="col-sm-3" for="poject_name"></label>
                <div class="col-sm-3">
                    <input type="text" class="form-control" id="poject_name" required v-model="above_area">
                    <!--<span></span>-->
                </div>
                <div class="col-sm-1 unit">
                    
                </div>
            </div>

            <div class="form-group">
                <label class="col-sm-3" for="poject_name"></label>
                <div class="col-sm-3">
                    <input type="text" class="form-control" id="poject_name" required v-model="above_coefficient">
                    <!--<span></span>-->
                </div>
            </div>

            <div class="form-group">
                <label class="col-sm-3" for="poject_name"></label>
                <div class="col-sm-3">
                    <input type="text" class="form-control" id="poject_name" required v-model="under_area">
                    <!--<span></span>-->
                </div>
                <div class="col-sm-1 unit">
                    
                </div>
            </div>

            <div class="form-group">
                <label class="col-sm-3" for="poject_name"></label>
                <div class="col-sm-3">
                    <input type="text" class="form-control" id="poject_name" required v-model="under_coefficient">
                    <!--<span></span>-->
                </div>
            </div>

            <div class="form-group">
                <label class="col-sm-3" for="poject_name"></label>
                <div class="col-sm-3">
                    <input type="text" class="form-control" id="poject_name" required v-model="contrast_price">
                </div>
                <div class="col-sm-1 unit">
                    
                </div>
            </div>

            <div class="form-group">
                <label class="col-sm-3" for="poject_name"></label>
                <div class="col-sm-7">
                    <textarea name="" id="" cols="30" rows="10" placeholder="" v-model="poject_intro"></textarea>
                </div>

            </div>


            <div>
                <a href="javascript:;" class="a-upload">
                    <input type="file" name="file" id="">
                    <span class="showFileName"></span>
                    <span class="fileerrorTip"></span>
                </a>

            </div>

            <div class="reg_btn">

                <button class="btn btn-default" type="button" @click="dataSubmit">
                    
                    <!--<router-link :to="{name:'startproject',params:{msg:poject_name}}" class="type_edit"></router-link>-->

                    <!--<router-link to="/startproject" ></router-link>-->
                </button>
            </div>
        </form>
    </div>
</template>

<script>
    export default {
        name: "CommonData",
        data() {
            return {
                poject_name: '',
                area: '10000',
                above_area: '5000',
                above_coefficient: '5',
                under_area: '5000',
                under_coefficient: '5',
                contrast_price: '200',
                poject_intro: '',
                // attachment: '',
                formData: {},
                is_formData:false,
                pojectId:null

            }
        },
        created() {
            // 
            this.$axios.interceptors.request.use(config => {
                // 
                //tokenheadertoken
                if (this.$store.state.token) {
                    config.headers.Authorization = this.$store.state.token
                }
                return config;
            }, error => {
                // 
                return Promise.reject(error);
            });
            this.$nextTick(function () {

            })
        },
        mounted() {
            const that = this;
            $(".a-upload").on("change", "input[type='file']", function (e) {
                const file = e.target.files[0];
                that.formData = new FormData();

                const filePath = $(this).val();
                if (filePath.indexOf(".zip") != -1 || filePath.indexOf(".rar") != -1) {
                    $(".fileerrorTip").html("").hide();
                    var arr = filePath.split('\\');
                    var fileName = arr[arr.length - 1];
                    $(".showFileName").html(fileName);

                    that.formData.append('file', file);

                    that.is_formData = true;
                    // that.attachment = that.formData;

                    //console.log(formData.get('file'))

                } else {
                    $(".showFileName").html("");
                    $(".fileerrorTip").html("").show();
                    return false
                }
            })
        },
        methods: {
            setMsg() {
                this.$emit('showState', false)
            },
            dataSubmit() {
                const that = this;

                if (this.poject_name == '' || this.area == '' || this.above_area == '' || this.above_coefficient == '' || this.under_area == '' || this.under_coefficient == '' || this.contrast_price == '' || this.poject_intro == ''||this.is_formData == false) {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                    return false;
                } else {

                    this.formData.append('name', this.poject_name);
                    this.formData.append('area', this.area);
                    this.formData.append('above_area', this.above_area)
                    this.formData.append('above_open_ratio', this.above_coefficient)
                    this.formData.append('below_area', this.under_area)
                    this.formData.append('below_open_ratio', this.under_coefficient)
                    this.formData.append('reference_price', this.contrast_price)
                    this.formData.append('intro', this.poject_intro)
                    this.formData.append('attachment', this.formData.get('file'))

                    // console.log(that.formData)

                    this.$axios({
                        method: 'post',
                        url: '/api/projects',
                        headers: {"Content-Type": "multipart/form-data"},
                        data: this.formData,
                        /*data: {
                            /!*name: this.poject_name,
                            area: this.area,
                            above_area: this.above_area,
                            above_open_ratio: this.above_coefficient,
                            below_area: this.under_area,
                            below_open_ratio: this.under_coefficient,
                            reference_price: this.contrast_price,
                            intro: this.poject_intro,*!/
                            attachment: this.formData
                        }*/
                    }).then(function (response) {

                        that.$message({
                            message: response.data.msg,
                            type: 'success'
                        })

                        let pojectMsg = {
                            name: that.poject_name,
                            area: that.area,
                            above_area: that.above_area,
                            above_open_ratio: that.above_coefficient,
                            below_area: that.under_area,
                            below_open_ratio: that.under_coefficient,
                            reference_price: that.contrast_price,
                            intro: that.poject_intro,
                            attachment: null,
                            projectId:response.data.data.id
                        }

                        // that.$store.commit('companyName', that.companyName)
                        // that.$store.commit('supplyTypeArr', typeArray)

                        that.$store.commit('pojectMsg', pojectMsg)

                        // console.log(that.$store.state.pojectMsg)
                        that.$router.push({name: 'startproject',params:{id:response.data.data.id}})

                        localStorage.removeItem('checkeds');

                        // this.$router.go(-1);
                        console.log(response)
                    }).catch(function (error) {
                        console.log(error);
                    });


                }


            }
        }
    }
</script>

<style scoped lang="less">
    .container {
        max-width: 800px;
        padding: 25px 100px;
        margin: auto;
        border: 2px solid #c6c6c6;
        height: 560px;
        position: absolute;
        background: #fff;
        top: 10%;
        left: 0;
        right: 0;
        z-index: 999;

        .back {
            position: absolute;
            top: 15px;
            right: 15px;
        }
    }

    .form-signin .form-signin-heading,
    .form-signin .checkbox {
        margin-bottom: 10px;
    }

    .form-signin .checkbox {
        font-weight: normal;
    }

    .form-signin .form-control {
        width: 100%;
        height: 35px;
        margin: 0 auto;
        position: relative;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        padding: 9px;
        font-size: 16px;
    }

    /*.form-signin .form-control:focus {
        z-index: 2;
    }*/

    .form-signin input[type="text"] {
        margin-bottom: 10px;
    }


    .login_title {
        margin: 0;
        padding: 0;
        margin-bottom: 15px;
        font-size: 16px;
        text-align: center;
    }

    /**/
    .form-signin .code_input {
        width: 64%;
        margin: 0;
        display: inline-block;
    }

    .form-signin .generate_code {
        width: 30%;
        margin: 0 0 0 15px;
        display: inline-block;
        font-size: 12px;
    }

    .reg_btn {
        width: 65%;
        text-align: center;
        margin: 0 auto;
        margin-top: 5px;
    }

    .reg_btn button {
        width: 20%;
        /*margin-top: 20px;*/
    }

    .form-group {
        overflow: hidden;
        margin-bottom: 0px;
    }

    .form-group div {
        padding: 0;
        overflow: hidden;
    }

    label {
        width: 32%;
        min-width: 190px;
        font-size: 14px;
        font-weight: normal;
        letter-spacing: 1px;
        border-radius: 4px;
        line-height: 33px;
        background: #fff;
        display: inline-block;
        text-align: left;
    }

    textarea {
        width: 100%;
        height: 60px;
        padding: 10px;
        font-size: 12px;
        border: 1px solid #ccc;
        border-radius: 4px;
    }

    .unit {
        margin-top: 10px;
        margin-left: 5px;
        text-align: left;
        font-size: 18px;
    }

    /*a  upload */
    .a-upload {
        padding: 4px 10px;
        height: 30px;
        line-height: 20px;
        position: relative;
        cursor: pointer;
        color: #888;
        /*background: #fafafa;*/
        /*border: 1px solid #ddd;*/
        /*border-radius: 4px;*/
        font-size: 12px;
        text-decoration: underline;
        overflow: hidden;
        display: inline-block;
        *display: inline;
        *zoom: 1
    }

    .a-upload input {
        position: absolute;
        font-size: 100px;
        right: 0;
        top: 0;
        opacity: 0;
        filter: alpha(opacity=0);
        cursor: pointer
    }

    .a-upload:hover {
        color: #444;
        background: #eee;
        border-color: #ccc;
    }


</style>